
              const blob = new Blob(["// src/worker/add.ts\nself.addEventListener(\"message\", (event) => {\n  const [a, b] = event.data;\n  self.postMessage(a + b);\n});\n"], { type: 'text/javascript' });
              export default () => {
                const worker = new Worker(window.URL.createObjectURL(blob));
                return worker;
              }
              